@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,400i,600,600i,700,700i");
.header {
  position: relative;
  overflow: hidden;
  min-height: 135px;
}

.header__logo {
  position: absolute;
  top: 23px;
  left: 50%;
  margin-left: -52px;
}

.header-nav {
  margin: 62px 0 0;
}

.header-nav__list {
  font-size: 13px;
  text-transform: uppercase;
  overflow: hidden;
}

.header-nav__item {
  margin: 0 25px;
}

.header-nav__item_left {
  margin: 0 50px 0 0;
  float: left;
}

.header-nav__item_right {
  margin: 0 0 0 50px;
  float: right;
}

.header-nav__item.active {
  font-weight: bold;
}

.header-nav__link, .header-nav__link:hover {
  color: #000;
}

@media (max-width: 979px) {
  .header-nav__item {
    margin: 0 10px;
  }
  .header-nav__item_left {
    margin: 0 20px 0 0;
    float: left;
  }
  .header-nav__item_right {
    margin: 0 0 0 20px;
    float: right;
  }
}

@media (max-width: 767px) {
  .header__logo {
    margin-left: -33px;
  }
  .header__logo img {
    width: 65px;
    height: 65px;
  }
}

.footer {
  overflow: hidden;
  padding: 8px 0 20px;
}

.footer__copyright, .footer__powered {
  font-size: 9px;
  margin: 3px 0 0;
}

.footer__copyright {
  float: left;
}

.footer__powered {
  float: right;
}

.footer-nav {
  text-align: center;
  font-size: 11px;
  text-transform: uppercase;
  overflow: hidden;
}

.footer-nav__item {
  display: inline;
  margin: 0 7px;
}

.footer-nav__link {
  color: #000;
}

@media (max-width: 979px) {
  .footer-nav__item {
    display: inline;
    margin: 0 2px;
  }
}

.main-page-blocks__row {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: space-between;
  flex-grow: 1;
  flex-shrink: 1;
  flex-flow: row wrap;
}

.main-page-blocks__block {
  position: relative;
  width: calc(50% - 5px);
  margin: 5px 0;
  text-align: center;
}

.main-page-blocks__block_black {
  background: #000;
}

.main-page-blocks__block_black, .main-page-blocks__block_black a {
  color: #f2f2f2;
}

.main-page-blocks__content {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  line-height: 1.2;
  transform: translateX(-50%) translateY(-50%);
}

.main-page-blocks__content p {
  margin: 13px 0;
}

.main-page-blocks__title {
  font-family: "Helvetica", Helvetica, Arial, sans-serif;
  font-size: 18px;
  margin: 0 0 42px;
}

@media (max-width: 767px) {
  .main-page-blocks__row_reverse-mobile .main-page-blocks__block:nth-child(2) {
    order: -1;
  }
  .main-page-blocks__block {
    position: relative;
    width: 100%;
  }
  .main-page-blocks__block_text {
    height: 250px;
  }
  .main-page-blocks__map {
    width: auto;
  }
}

body {
  font: normal normal normal 11px "Open Sans", sans-serif;
}

ul {
  margin: 0;
}

li {
  list-style-type: none;
}

.wrapper {
  min-width: 320px;
  max-width: 1185px;
  margin: 0 auto;
  padding: 0 20px;
}

@media (max-width: 767px) {
  .wrapper {
    padding: 0;
  }
}
