.header {
    position: relative;
    overflow: hidden;
    min-height: 135px;

    &__logo {
        position: absolute;
        top: 23px;
        left: 50%;
        margin-left: -52px;
    }
}
.header-nav {
    margin: 62px 0 0;

    &__list {
        font-size: 13px;
        text-transform: uppercase;
        overflow: hidden;
    }
    &__item {
        margin: 0 25px;

        &_left {
            margin: 0 50px 0 0;
            float: left;
        }
        &_right {
            margin: 0 0 0 50px;
            float: right;
        }
        &.active {
            font-weight: bold;
        }
    }
    &__link {
        &, &:hover {
            color: #000;
        }
    }
}

@media (max-width: 979px) {
    .header-nav {
        &__item {
            margin: 0 10px;

            &_left {
                margin: 0 20px 0 0;
                float: left;
            }
            &_right {
                margin: 0 0 0 20px;
                float: right;
            }
        }
    }
}

@media (max-width: 767px) {
    .header {
        &__logo {
            margin-left: -33px;
            img {
                width: 65px;
                height: 65px;
            }
        }
    }
}
